import React, { useState } from "react";
import "./SideBar.css";

const SideBar = ({ onTypeSelect }) => {
  const [isOpen, setIsOpen] = useState(false);
  const [selectedIcon, setSelectedIcon] = useState(null);

  const handleMouseEnter = () => {
    setIsOpen(true); // 
  };

  const handleMouseLeave = () => {
    setIsOpen(false); // 
  };

  const handleIconClick = (type) => { 
    const newSelectedIcon = selectedIcon === type ? null : type; 
    setSelectedIcon(newSelectedIcon); // 
    onTypeSelect(newSelectedIcon); // 
  };
  

  return (
    <div 
      className={`side-bar ${isOpen ? "open" : ""}`}
      style={{
        backgroundImage: `url('${process.env.PUBLIC_URL}/homepage/section-two/side-bar-bg.png')`,
        backgroundSize: "cover",
        backgroundPosition: "center",
        backgroundRepeat: "no-repeat",
      }}
      onMouseEnter={handleMouseEnter} // 
      onMouseLeave={handleMouseLeave} // 
    >
      <div className="icon-group">
        <div 
          className="icon" 
          onClick={() => handleIconClick("")}
          style={{ opacity: selectedIcon === "" ? 1 : 0.5 }}
          data-tooltip=""
        >
          <img src={`${process.env.PUBLIC_URL}/homepage/section-two/icons/icon-.png`} alt="Icon " />
        </div>
        <div className="line">
          <img src={`${process.env.PUBLIC_URL}/homepage/section-two/icons/icon-line.png`} alt="Line" />
        </div>
        <div 
          className="icon" 
          onClick={() => handleIconClick("")}
          style={{ opacity: selectedIcon === "" ? 1 : 0.5 }} 
          data-tooltip=""
        >
          <img src={`${process.env.PUBLIC_URL}/homepage/section-two/icons/icon-.png`} alt="Icon " />
        </div>
        <div className="line">
          <img src={`${process.env.PUBLIC_URL}/homepage/section-two/icons/icon-line.png`} alt="Line" />
        </div>
        <div 
          className="icon" 
          onClick={() => handleIconClick("")}
          style={{ opacity: selectedIcon === "" ? 1 : 0.5 }} 
          data-tooltip=""
        >
          <img src={`${process.env.PUBLIC_URL}/homepage/section-two/icons/icon-.png`} alt="Icon " />
        </div>
        <div className="line">
          <img src={`${process.env.PUBLIC_URL}/homepage/section-two/icons/icon-line.png`} alt="Line" />
        </div>
        <div 
          className="icon" 
          onClick={() => handleIconClick("")}
          style={{ opacity: selectedIcon === "" ? 1 : 0.5 }} 
          data-tooltip=""
        >
          <img src={`${process.env.PUBLIC_URL}/homepage/section-two/icons/icon-.png`} alt="Icon " />
        </div>
      </div>
    </div>
  );
};

export default SideBar;